import { Link } from "react-scroll";

function Header() {
  return (
    <div className="bg-dark flex items-center justify-center shadow-sm sticky top-0 z-50">
      <div className="p-2 pr-10 flex space-x-4">
        <Link to="about" className="cursor-pointer" smooth={true} offset={-200}>
          <p className="text-light font-bold">About</p>
        </Link>
        <Link to="works" className="cursor-pointer" smooth={true} offset={-50}>
          <p className="text-light font-bold">Portfolio</p>
        </Link>
      </div>
    </div>
  );
}

export default Header;
