import Header from "./components/Header";
import Particles from "react-particles-js";
import { params } from "./particles";
import { frontend, backend, thirdparty, works } from "./data";

function App() {
  return (
    <div className=" bg-dark  ">
      <header className=""></header>
      <Header />
      <div className="overflow-hidden">
        <div className="bg-dark h-screen ">
          <div className="relative flex items-center justify-center    ">
            <Particles params={params} />
            <div className="bg-dark absolute top-28 xl:top-28 text-center ">
              <p className="font-bold xl:text-6xl text-3xl xl:mb-10 text-light md:text-6xl z-50">
                Hi, my name is Youssef
              </p>
              <p className=" xl:text-3xl text-xl mt-10 text-light md:text-3xl md:mt-4">
                a full stack web developer
              </p>
              <div className="flex mt-8 relative space-x-3 items-center justify-center xl:mt-5 xl:space-x-6">
                <div className="w-10 xl:w-10 z-40">
                  <a href="https://github.com/youssefmohamedsabry">
                    <img
                      src="git.png"
                      className=" cursor-pointer object-contain transform hover:scale-125 transition-all duration-200 ease-out"
                      alt=""
                    />
                  </a>
                </div>
                <a
                  className=" transform hover:scale-125 transition-all duration-200 ease-out z-40 cursor-pointer"
                  href="https://www.linkedin.com/in/youssef-sabry-3445171b8/"
                >
                  <img
                    src="ln.png"
                    className="w-10 xl:w-10 object-contain"
                    alt=""
                  />
                </a>
              </div>
            </div>
          </div>
        </div>
        <div className=" flex flex-col justify-center items-center space-y-96 ">
          <section
            id="about"
            className=" rounded-xl w-10/12 xl:w-1/2 text-center space-y-4 xl:space-y-10 p-10"
          >
            <p className="text-light text-4xl font-semibold ">About me</p>
            <p className="text-light text-2xl">
              I'm an university student who is interested in software field.
              started this journey by taking 2 courses provided by Udacity and
              working in freelancing websites
            </p>
          </section>
          <section className=" rounded-xl w-10/12 xl:w-1/2 text-center space-y-16 p-10">
            <p className="text-light text-3xl font-semibold">My skills</p>
            <div className="grid lg:grid-cols-3 gap-y-16  ">
              <div className="space-y-6">
                <p className="text-light text-2xl font-semibold border-b border-t border-light shadow-xl">
                  Frontend
                </p>
                <ul className="space-y-2">
                  {frontend.map((skill) => (
                    <li className="text-light ">{skill}</li>
                  ))}
                </ul>
              </div>
              <div className="space-y-6">
                <p className="text-light text-2xl font-semibold border-b border-t border-light shadow-xl">
                  Backend
                </p>
                <ul className="space-y-2">
                  {backend.map((skill) => (
                    <li className="text-light">{skill}</li>
                  ))}
                </ul>
              </div>
              <div className="space-y-6">
                <p className="text-light text-2xl font-semibold border-b border-t border-light shadow-xl bg-dark z-50">
                  Third party
                </p>
                <ul className="space-y-2">
                  {thirdparty.map((skill) => (
                    <li className="text-light">{skill}</li>
                  ))}
                </ul>
              </div>
            </div>
          </section>
          <section
            id="works"
            className="rounded-xl w-10/12 xl:w-10/12 text-center space-y-16 "
          >
            <p className="text-light text-3xl font-semibold">My work</p>
            <div className="grid lg:grid-cols-3 gap-y-16 gap-x-16">
              {works.map((work) => (
                <div className="bg-light rounded-xl xl:p-5 gap-y-5 z-40 flex flex-col items-center  ">
                  <h2 className="font-bold text-lg border-b shadow-xld">
                    {work.name}
                  </h2>
                  <div className="flex flex-1">
                    <img className="border  flex-1 " src={work.img} alt="img" />
                  </div>
                  <div>
                    <div className="flex gap-x-1 mt-2 flex-wrap p-3 border-b border-t gap-y-1">
                      {work.tech.map((t) => (
                        <div className="bg-dark rounded-full p-1 font-semibold text-sm text-light">
                          {t}
                        </div>
                      ))}
                    </div>
                  </div>
                  <div className="w-20 xl:w-20 z-50 flex gap-x-4 items-center justify-center mb-5 ">
                    <a href={work.live} className="text-xs ">
                      Live demo
                    </a>
                    <a href={work.git} className="">
                      <img
                        src="git.png"
                        className=" cursor-pointer object-contain transform hover:scale-125 transition-all duration-200 ease-out"
                        alt=""
                      />
                    </a>
                  </div>
                </div>
              ))}
            </div>
          </section>
        </div>
      </div>
    </div>
  );
}

export default App;
